.input_box {
    position: relative;
    margin-top: 23px;
}

.checkbox_box {
    margin-top: 10px;
}

.button_box {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 33px;
}

.button_box button {
    width: 270px;
    font-size: 18px;
}

.registr_btn {
    margin-top: 10px;
    background: #1D1D1D;
    color: #fff;
}

.forget_password {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: -6px;
}

.forget_password__title {
    font-family: Roboto, 'sans-serif';
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 19px;
    color: #949494;
}

.forget_password span {
    font-family: Roboto, 'sans-serif';
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 19px;
    color: #FFD80E;
    margin-left: 5px;
    cursor: pointer;
}

.login_with {
    width: 447px;
    border-radius: 10px;
    border: 1px solid #525251;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    padding: 19px 20px 21px 39px;
    margin-top: 3px;
}

.login_with__title {
    font-family: Roboto, 'sans-serif';
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    color: #949494;
    padding: 0;
    margin: 0;
}

.icons {
    margin-left: 31px;
    cursor: pointer;
}

.icons:nth-child(2) {
    margin-left: 25px;
}

.capsLockInputBox {
    position: relative;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.inputs_regist {
    width: 346px !important;
}

